import React, { Component } from 'react'

export default (WrappedComponent) => (
    class extends Component {
        constructor(props) {
            super(props)
            this.props = props
        }

        componentDidMount() {

        }

        componentWillUnmount() {

        }

        render() {
            return <WrappedComponent {...this.props} />
        }
    }
)


