import React, {PureComponent, Fragment} from 'react';
import PropTypes from 'prop-types'
import styled from 'styled-components'
import { debounce } from 'throttle-debounce'
import { pdfjs, Document, Page } from 'react-pdf'
import { AutoSizer, List } from 'react-virtualized';
import SectionMessage from '@atlaskit/section-message'
import Button, { ButtonGroup } from '@atlaskit/button';

import PageRenderer from './PageRenderer'
import Loader from './Loader'
import withTracking from './withTracking'

import VidFullScreenOnIcon from '@atlaskit/icon/glyph/vid-full-screen-on';
import VidFullScreenOffIcon from '@atlaskit/icon/glyph/vid-full-screen-off';

import MediaServicesZoomInIcon from '@atlaskit/icon/glyph/media-services/zoom-in';
import MediaServicesZoomOutIcon from '@atlaskit/icon/glyph/media-services/zoom-out';

import ArrowLeftIcon from '@atlaskit/icon/glyph/arrow-left';
import ArrowRightIcon from '@atlaskit/icon/glyph/arrow-right';

import 'react-pdf/dist/Page/AnnotationLayer.css'

// pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const Viewer = styled.div`
  display: flex;
  flex-direction: column;
  background-color: #404040;
  width: 100%;
  height: 100%;
  overflow: hidden;

  & .react-pdf__message {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: 16px;
  }
`

class PDFViewer extends PureComponent {
  static propTypes = {
    scale: PropTypes.number.isRequired
  }

  static defaultProps = {
    scale: 1,
    margin: 32
  }

  constructor(props) {
    super(props)

    this.state = {
      pdf: null,
      cachedPageDimensions: null,
      responsiveScale: 1,
      isFullscreen: !!document.fullscreenElement,
      currentPage: 1
    }
  }

  componentDidMount() {
    this.ref.addEventListener("fullscreenchange", this.handleFullscreenChange.bind(this), false)
    document.addEventListener("keydown", this.handleKeydown.bind(this), false)
    window.addEventListener("resize", debounce(200, this.handleResize.bind(this)), false)
  }

  componentWillUnmount() {
    this.ref.removeEventListener("fullscreenchange", this.handleFullscreenChange.bind(this), false)
    document.removeEventListener("keydown", this.handleKeydown.bind(this), false)
    window.removeEventListener("resize", debounce(200, this.handleResize.bind(this)), false)
  }

  /**
    * Load all pages so we can cache all page dimensions.
    *
    * @param {Object} pdf
    * @returns {void}
    */
  cachePageDimensions(pdf) {
    const promises = Array
      .from({ length: pdf.numPages }, (v, i) => i + 1)
      .map(pageNumber => pdf.getPage(pageNumber))

    let height = 0

    // Assuming all pages may have different heights. Otherwise we can just
    // load the first page and use its height for determining all the row
    // heights.
    Promise.all(promises).then(pages => {
      const pageDimensions = new Map
      for (const page of pages) {
        const w = page.view[2] * this.props.scale
        const h = page.view[3] * this.props.scale

        pageDimensions.set(page.pageIndex + 1, [w + 64, h + 64])
        height += h;
      }

      this.setState({
        cachedPageDimensions: pageDimensions,
        initialContainerHeight: height,
        containerHeight: height,
        isDocument: (pages[0].view[3] / pages[0].view[2] > 1.0) // assume page view if first page has a vertical format
      }, this.handleResize)
    })
  }

  handleResize() {
    const { currentPage, cachedPageDimensions, isDocument } = this.state
    if (!isDocument) {
      const { innerWidth, innerHeight } = window
      const [w, h] = cachedPageDimensions.get(currentPage)
      let scale = (innerHeight * (w / h) > innerWidth) ? innerWidth / w : innerHeight / h
      this.setState({
        responsiveScale: scale
      })
    }
  }

  computeRowHeight({ index }) {
    const { cachedPageDimensions, responsiveScale } = this.state
    if (cachedPageDimensions && responsiveScale) {
      return (cachedPageDimensions.get(index + 1)[1] / responsiveScale)
    }

    return 768; // Initial height
  }

  onDocumentLoadSuccess(pdf) {
    this.setState({ pdf })
    this.cachePageDimensions(pdf)
  }

  rowRenderer = (props) => (
    <PageRenderer data={{ scale: this.state.responsiveScale }} {...props} />
  )

  renderError = (error) => (
    <SectionMessage appearance="error" title="An error occured" style={{ width: '100%' }}>
      { error }
    </SectionMessage>
  )

  handleZoomIn() {
    this.setState({
      responsiveScale: this.state.responsiveScale + .1
    })
  }

  handleZoomOut() {
    this.setState({
      responsiveScale: this.state.responsiveScale - .1
    })
  }

  handleFullscreenChange() {
    this.setState({
      isFullscreen: !!document.fullscreenElement
    })
  }

  toggleFullscreen() {
    if (!document.fullscreenElement) {
      this.ref.requestFullscreen();
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen(); 
      }
    }
  }

  handlePreviousPage() {
    this.setState({ currentPage: this.state.currentPage - 1 })
    this.handleResize()
  }

  handleNextPage() {
    this.setState({ currentPage: this.state.currentPage + 1 })
    this.handleResize()
  }

  handleKeydown(event) {
    const { currentPage, pdf } = this.state

    switch(event.keyCode) {
      case 37:
        currentPage > 1 && this.handlePreviousPage()
        break
      case 39:
        currentPage < pdf.numPages && this.handleNextPage()
        break
    }
  }

  render() {
    const { file } = this.props
    const { error, cachedPageDimensions, pdf, isFullscreen, isDocument, currentPage } = this.state

    return (
      <Viewer innerRef={ ref => this.ref = ref }>
        <div style={{ display: 'flex', background: 'rgb(244, 245, 247)', textAlign: 'right', justifyContent: 'space-between' }}>
          {cachedPageDimensions && (
            <ButtonGroup>
              <Button appearance="subtle" isDisabled={currentPage === 1} iconBefore={<ArrowLeftIcon />} onClick={() => this.handlePreviousPage()} />
              <div style={{ display: 'flex', alignItems: 'center' }}>
                {this.state.currentPage} of {pdf.numPages}
              </div>
              <Button appearance="subtle" isDisabled={currentPage === pdf.numPages} iconBefore={<ArrowRightIcon />} onClick={() => this.handleNextPage()} />
            </ButtonGroup>
          )}
          <ButtonGroup>
            <Button appearance="subtle" iconBefore={<MediaServicesZoomInIcon />} onClick={() => this.handleZoomIn()} />
            <Button appearance="subtle" iconBefore={<MediaServicesZoomOutIcon />} onClick={() => this.handleZoomOut()} />
            <Button appearance="subtle" iconBefore={isFullscreen ? <VidFullScreenOffIcon /> : <VidFullScreenOnIcon />} onClick={() => this.toggleFullscreen()} />
          </ButtonGroup>
        </div>
        <div style={{ flex: 1 }}>
          <AutoSizer>
            {({ height, width }) => (
              <Document
                file={file}
                loading={<Loader invertColor />}
                onLoadSuccess={this.onDocumentLoadSuccess.bind(this)}
                onLoadError={(error) => this.renderError(`Error while loading document: ${error}`)}
                options={{
                  cMapUrl: 'cmaps/',
                  cMapPacked: true,
                }}
              >
                {cachedPageDimensions && (
                  <Fragment>
                    { isDocument ? (
                      <List
                        width={width}
                        height={height}
                        rowCount={pdf.numPages}
                        rowHeight={this.computeRowHeight.bind(this)}
                        rowRenderer={this.rowRenderer}
                        overscanRowCount={3}
                        estimatedRowSize={this.computeRowHeight({ index: 0 })}
                      />
                    ) : (
                      <PageRenderer data={{ scale: this.state.responsiveScale }} index={currentPage - 1} style={{ width, height }} />
                    ) }
                  </Fragment>
                )}
              </Document>
            )}
          </AutoSizer>
        </div>
      </Viewer>
    )
  }
}

export default withTracking(PDFViewer)


